import React, { useEffect, useState } from 'react'
import cl from './Login.module.css'
import { useUsers } from '../../store/users'
import { useIsAuth } from '../../store/isAuth'
import { useLocation, useNavigate } from 'react-router-dom'

const Login = () => {
  const {users, addUser} = useUsers()
  const {login} = useIsAuth()
  const navigate = useNavigate()

  const prevLocation = useLocation()

  const [userLog, setUserLog] = useState({
    login: '',
    password: ''
  })
  
  const [registrationForm, setRegistrationForm] = useState(false)
  const [userNotFound, setUserNotFound] = useState(false)

  const examination = event => {    
    if (event) event.preventDefault()
    const isUser = users.find(user => user.userName.toLowerCase() === userLog.login.toLowerCase() && user.password === userLog.password)
    if (isUser) {
        login(isUser)
        navigate(prevLocation.state)
    }
    else setUserNotFound(true)
  }

  const registration = event => {
    event.preventDefault()
    addUser(userLog)
  }

  useEffect(() => {
    if(registrationForm) examination()
  }, [users])

  return (
    <div className={cl.login}>
        <div className={cl.wrapperForm}>
            <div className={cl.close} onClick={() => navigate(prevLocation.state)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                    <path d="M18.3536 5.64645C18.5488 5.84171 18.5488 6.15829 18.3536 6.35355L6.35355 18.3536C6.15829 18.5488 5.84171 18.5488 5.64645 18.3536C5.45118 18.1583 5.45118 17.8417 5.64645 17.6464L17.6464 5.64645C17.8417 5.45118 18.1583 5.45118 18.3536 5.64645Z" fill="#414141"/>
                    <path d="M5.64645 5.64645C5.84171 5.45118 6.15829 5.45118 6.35355 5.64645L18.3536 17.6464C18.5488 17.8417 18.5488 18.1583 18.3536 18.3536C18.1583 18.5488 17.8417 18.5488 17.6464 18.3536L5.64645 6.35355C5.45118 6.15829 5.45118 5.84171 5.64645 5.64645Z" fill="#414141"/>
                </svg>
            </div>
            {registrationForm?
            <form className={cl.form} onSubmit={e => registration(e)}>
                <div className={cl.formTitle}>
                    Регистрация
                </div>
                <div className={cl.formTel}>
                    <label htmlFor="login">Логин</label>
                    <input value={userLog.login} onChange={e => setUserLog({...userLog, login: e.target.value})} id='login' type="text" required/>
                    <label htmlFor="pass">Пароль</label>
                    <input value={userLog.password} onChange={e => setUserLog({...userLog, password: e.target.value})}id='pass' type="password" required/>
                </div>
                <div className={cl.formLogin} >
                    <button type='submit'>Зарегистрироваться</button>
                </div>
                <div className={cl.formBtns} onClick={() => setRegistrationForm(false)}>
                    <button >Вернуться</button>
                </div>
            </form>
            :
            <form className={cl.form} onSubmit={e => examination(e)}>
                <div className={cl.formTitle}>
                    Вход
                </div>
                <div className={cl.formTel}>
                    <label htmlFor="login">Логин</label>
                    <input value={userLog.login} onChange={e => setUserLog({...userLog, login: e.target.value})} id='login' type="text" required/>
                    <label htmlFor="pass">Пароль</label>
                    <input value={userLog.password} onChange={e => setUserLog({...userLog, password: e.target.value})}id='pass' type="password" required/>
                </div>
                {userNotFound ? 
                    <div className={cl.notFound}>
                        Неправильный логин или пароль
                    </div>
                : null}
                <div className={cl.formLogin} >
                    <button type='submit'>Войти</button>
                </div>
                <div className={cl.formBtns}>
                    <button onClick={() => setRegistrationForm(true)}>Регистрация</button>
                    <button>Забыли пароль?</button>
                </div>
            </form>          
            }
        </div>
    </div>
  )
}

export default Login