import React from 'react'
import cl from './About.module.css'
import Container from '../../components/UI/container/Container'

const About = () => {
    const services = [
        { id: 1, title: 'Мы занимаемся розничной торговлей', subtitle: 'Более 20 лет.' },
        { id: 2, title: 'Основная миссия компании', subtitle: 'Максимальное качество товаров и услуг по доступной цене.' },
        { id: 3, title: 'Отличительная черта нашей сети', subtitle: 'Здоровая и полезная продукция местного производства внаших магазинах.' },
    ]


    return (
        <Container>
            <title></title>
            <section>
                <div className={cl.about}>
                    <div className={cl.top}>
                        <div className={cl.title}>О компании</div>
                        <div className={cl.subtitle}>Мы непрерывно развиваемся и работаем над совершенствованием сервиса, заботимся о наших клиентах, стремимся к лучшему будущему.</div>
                    </div>
                    <div className={cl.middle}>
                        {services.map(service =>
                            <div className={cl.item}>
                                <div className={cl.title}>{service.title}</div>
                                <div className={cl.subtitle}>{service.subtitle}</div>
                                <div className={cl.icon}>
                                    <img src="./assets/icons/check.svg" alt="check" />
                                </div>
                            </div>
                        )}
                    </div>
                    <div className={cl.bottom}>
                        <div className={cl.logo}>
                            <img src="./assets/logo/logo-about.png" alt="logo" />
                        </div>
                        <div className={cl.text}>
                            Спасибо за то, что вы с нами. Северяночка, везет всегда!
                        </div>
                    </div>
                </div>
            </section>
        </Container>
    )
}

export default About