import React, { useMemo, useState } from "react";
import cl from "./UserBar.module.css";
import { useIsAuth } from "../../../store/isAuth";
import { useNavigate } from "react-router-dom";
import { useUsers } from "../../../store/users";

const UserBar = () => {
  const isAuthUser = useIsAuth((state) => state.isAuthUser);
  const isAuth = useIsAuth((state) => state.isAuth);
  const logout = useIsAuth((state) => state.logout);
  const {users} = useUsers()

  const user = users.find(user => user.userName === isAuthUser.userName) || {}

  const navigate = useNavigate();

  const [barActive, setBarActive] = useState(false)
  const links = useMemo(() => {
    return [
        { name: "Избранное", path: "favourite", count: user.favourite ? user.favourite.length : 0},
        { name: "Заказы", path: "orders", count: user.orders ? user.orders.length : 0},
        { name: "Корзина", path: "basket", count: user.basket ? user.basket.length : 0},
        { name: "Выйти", path: "logout",},
    ] 
  }, [users])

  const linkNavigate = (path) => {
    if (path === 'logout') {
        logout()
    }
    else navigate(`/${path}`)
    setBarActive(false)
  }

  

  return (
    <nav>
        <div className={[cl.links, barActive ? cl.linksActive : null].join(' ')}>
            {isAuth ?
                links.map(link =>
                    <div key={link.path} className={cl.link} onClick={() => linkNavigate(link.path)}>
                        <div className={cl.linkIMG}>
                            <img src={`../../../../assets/icons/${link.path}.svg`} alt={link.path} />     
                            {link.count ? <span className={cl.countLink}>{link.count}</span> : null}
                        </div>
                        <div className={cl.linkTitle}>{link.name}</div>
                    </div>    
                )
            :
            <div className={cl.link}>
                <button onClick={() => navigate(`/login`, {state: window.location.pathname})}>Авторизироваться</button>
            </div>
            }
        </div>
        <div className={cl.user} onClick={() => setBarActive(prev => !prev)}>
            <div className={cl.userIMG}>
                <img src={`../../../../assets/user/avatar/${isAuthUser.userImg || `default`}.svg`} alt="avatar" />
            </div>
            <div className={[cl.userIMG, cl.userIMGBottom].join(' ')} onClick={() => isAuth ? null : navigate(`/login`, {state: window.location.pathname})}>
                <img src={`../../../../assets/user/avatar/${isAuthUser.userImg || `default`}.svg`} alt="avatar" />
                <span>Профиль</span>
            </div>
            <div className={cl.userName}>
                {isAuthUser.userName}
            </div>
            {isAuth && <div className={cl.userArrow }>
                <img src='../../../../assets/icons/arrow.svg' alt="arrow" />
            </div>}
        </div>
    </nav>
  );
};

export default UserBar;
