import { Map, Placemark, YMaps } from '@pbe/react-yandex-maps'
import React, { useMemo, useState } from 'react'
import Container from '../UI/container/Container'
import cl from './Shops.module.css'

const Shops = () => {
    const shops =[
        {name: 'д. Щельяюр', coordsLongitude: 50.864509, coordsLatitude: 13.994834},
        {name: 'д. Вертеп', coordsLongitude: 41.584954, coordsLatitude: -3.485546},
        {name: 'д. Краснобор', coordsLongitude: 56.399444, coordsLatitude: 5.170488},
        {name: 'д. Диюр', coordsLongitude: 63.741962, coordsLatitude: 14.797919},
    ]
   
    const [activeOurShop, setActiveOurShop] = useState(shops.length ? 0 : null)

    const coords = useMemo(() => {
        return [shops[activeOurShop].coordsLongitude, shops[activeOurShop].coordsLatitude ]
    }, [activeOurShop])

    return (
        <Container>
            <title>Наши магазины</title>
            <section>
                <div className={cl.shops}>
                    <div className={cl.buttons}>
                        {shops.map((shop, index) =>
                            <button key={index} onClick={() => setActiveOurShop(index)} className={activeOurShop === index ? cl.active : null} >{shop.name}</button>    
                        )}
                    </div>
                    <div className={cl.map}>
                        <YMaps>
                            {isFinite(activeOurShop) && 
                            <Map width='100%' state={{ center: coords, zoom: 9 }}>
                                <Placemark  geometry={coords} />
                            </Map>} 
                        </YMaps> 
                    </div>
                </div>
            </section>
        </Container>
    )
}

export default Shops