import React from 'react'
import cl from './MyFooter.module.css'
import { useNavigate } from 'react-router-dom'

const MyFooter = () => {
    const navigate = useNavigate()

    const linkLeft = [
        { name: 'О компании', path: '/about' },
        { name: 'Контакты', path: '/contacts' },
        { name: 'Вакансии', path: '/vacations' },
        { name: 'Статьи', path: '/articles' },
        { name: 'Политика обработки персональных данных', path: '/policy' },
    ]
    const linkRight = [
        { name: 'instagram', path: '/' },
        { name: 'vk', path: '/' },
        { name: 'facebook', path: '/' },
        { name: 'od', path: '/' },
    ]
    return (
        <footer>
            <div className={cl.leftSide}>
                <div className={cl.link}>
                    <img src="./assets/logo/logo.svg" alt="logo" />
                </div>
                {linkLeft.map(link =>
                    <div onClick={() => navigate(`${link.path}`)} key={link.name} className={cl.link}>
                        {link.name}
                    </div>
                )}
            </div>
            <div className={cl.rightSide}>
                {linkRight.map(link =>
                    <div onClick={() => navigate(`${link.path}`)} key={link.name} className={cl.link}>
                        <img src={`./assets/icons/${link.name}.svg`} alt={link.name} />
                    </div>
                )}
                <div className={cl.link}>
                    <img src="./assets/icons/phone.svg" alt="phone" />
                    <a href="tel:+37525">+ 7929 689 5487</a>
                </div>
            </div>
        </footer>
    )
}

export default MyFooter