import React, { useMemo } from 'react'
import Container from '../../components/UI/container/Container'
import CatalogProduct from '../Catalog/CatalogProduct'
import { useUsers } from '../../store/users'
import { useProducts } from '../../store/products'
import { useIsAuth } from '../../store/isAuth'

const MyFavourite = () => {
  const {products} = useProducts()
  const {users} = useUsers()
  const {isAuthUser} = useIsAuth()
  const user = useMemo(() => users.find(user => user.userName === isAuthUser.userName)) || {}
  const userFavourite = useMemo(() => user.favourite)
  const userFavouriteProducts = useMemo(() => products.filter(pr => userFavourite.includes(pr.name)))

  return (
    <Container>
        <title>
            Избранное
        </title>
        <section>
            <CatalogProduct childrenProducts={userFavouriteProducts}/>
        </section>
    </Container>
  )
}

export default MyFavourite