import React, { useEffect, useMemo, useState } from 'react'
import './App.css'
import MyHeader from './components/myHeader/MyHeader'
import ProductsSection from './components/products/ProductsSection'

import AppRouter from './components/appRouter/AppRouter'
import { useProducts } from './store/products'
import { useCatalogs } from './store/catalogs'
import Slider from './components/sliders/Slider'
import MyFooter from './components/myFooter/MyFooter'


const App = () => {
  const {products} = useProducts()
  const {catalogs} = useCatalogs()
  const [search, setSearch] = useState('')

  const searchedProducts = useMemo(() => {
    return products.filter(p => p.name.toLowerCase().includes(search.toLowerCase()))
  }, [search])
  return (
    <div>
      <MyHeader catalogs={catalogs} checkSearch={(e) => setSearch(e)}></MyHeader>
      <Slider>Доставка бесплатно от 1000 BYN</Slider>
      <div className='middleContainer'>

        {search.length ?
        <ProductsSection section={{name: 'Результаты поиска', nameButton: '', limitCard: 30}} productsSection={searchedProducts}/>  
        :      
        <AppRouter />
        } 
        {/* <Loader /> */}

      </div>
      <MyFooter />
      <MyHeader className={'headerBottom'} catalogs={catalogs} checkSearch={(e) => setSearch(e)}></MyHeader>
    </div>
  )
}

export default App