import React from 'react'
import cl from './MyHeader.module.css'
import UserBar from '../UI/userBar/UserBar'
import { useNavigate } from 'react-router-dom'


const MyHeader = ({ className, search, checkSearch, catalogs }) => {
  const navigate = useNavigate()
  return (
    <header className={cl[className]}>
      <div className={cl.wrapper}>
        <div className={cl.topContainer}>
          <div className={cl.logoContainer} onClick={() => navigate(`/`)}>
            <img src="./assets/logo/logo-header.svg" alt="logo" />
          </div>
          <div className={cl.catalogContainer}>
            <button onClick={() => navigate(`/catalog`)}>
              <img src="./assets/icons/catalog.svg" alt="catalog" />
              <span>Каталог</span>
            </button>
          </div>
          <div className={cl.inputContainer}>
            <input type="text" placeholder='Найти товар' value={search} onChange={e => checkSearch(e.target.value)} />
            <img src="./assets/icons/search.svg" alt="search" />
          </div>
        </div>

        <div className={cl.bottomContainer}>
          <div className={cl.bottom} onClick={() => navigate(`/`)}>
            <img src="./assets/icons/home.svg" alt="home" />
            <span>Главная</span>
          </div>
          <div className={cl.bottom} onClick={() => navigate(`/catalog`)}>
            <img src="./assets/icons/catalogBottom.svg" alt="catalogBottom" />
            <span>Каталог</span>
          </div>
          <div className={[cl.inputContainer, cl.bottom].join(' ')} >
            <input type="text" placeholder='Найти товар' value={search} onChange={e => checkSearch(e.target.value)} />
            <img src="./assets/icons/search.svg" alt="search" />
          </div>
        </div>

        <UserBar />
      </div>
    </header>
  )
}

export default MyHeader