import React, { useEffect, useMemo, useState } from 'react'
import cl from './Orders.module.css'
import ProductCard from '../../components/products/ProductCard'

const Order = ({ order, products }) => {
    const [ordersCard, setOrdersCard] = useState(4)

    const productsInOrder = useMemo(() => {
        return order.orderProducts.map(ordProduct => ({ ...products.reduce((obj, product) => ordProduct.name === product.name ? product : obj, {}), count: ordProduct.count }))
    }, [])

    const priceInOrder = useMemo(() => {
        return productsInOrder.reduce((sum, ordProduct) => {
            if (ordProduct.promo) return ((ordProduct.price - (ordProduct.price * ordProduct.promo / 100)) * ordProduct.count) + sum
            else return (ordProduct.price * ordProduct.count) + sum
        }, 0)
    }, [])

    useEffect(() => {
    }, [])

    return (
        <div className={cl.order}>
            <div className={cl.header}>
                <div className={cl.datetime}>
                    <div className={cl.date}>
                        {order.date}
                    </div>
                    <div className={cl.time}>
                        {order.time}
                    </div>
                </div>
                <div className={
                    order.result === 'Получен' ? [cl.result, cl.received].join(' ') :
                        order.result === 'Не доставили' ? [cl.result, cl.return].join(' ') :
                            order.result === 'Возврат' ? [cl.result, cl.return].join(' ') : cl.result
                }>
                    {order.result}
                </div>
                <div className={cl.allPrice}>
                    {priceInOrder} BYN
                </div>
                <button className={cl.btns}>
                    <img src="./assets/icons/calendar.svg" alt="calendar" />
                    Когда доставить
                </button>
            </div>
            <div className={cl.body}>
                <div className={cl.bodyCards} style={{ display: 'flex', flexWrap: 'wrap' }}>
                    {productsInOrder.map((product, index) =>
                        index < ordersCard ?
                            <ProductCard key={index} product={product} />
                            : null
                    )}
                </div>
                {
                    productsInOrder.length > ordersCard ?
                        <div className={cl.bodyButton}>
                            <button onClick={() => setOrdersCard(ordersCard * 2)}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="24" viewBox="0 0 25 24" fill="none">
                                    <path d="M2.06643 12C2.08302 12.0301 2.10133 12.063 2.12133 12.0986C2.23999 12.3096 2.41836 12.6133 2.65439 12.9787C3.12709 13.7107 3.82783 14.6853 4.73977 15.658C6.57276 17.6132 9.19975 19.5 12.5 19.5C15.8002 19.5 18.4272 17.6132 20.2602 15.658C21.1722 14.6853 21.8729 13.7107 22.3456 12.9787C22.5816 12.6133 22.76 12.3096 22.8787 12.0986C22.8987 12.0631 22.917 12.0301 22.9336 12C22.917 11.9699 22.8987 11.937 22.8787 11.9014C22.76 11.6904 22.5816 11.3867 22.3456 11.0213C21.8729 10.2893 21.1722 9.3147 20.2602 8.34197C18.4272 6.38678 15.8002 4.5 12.5 4.5C9.19975 4.5 6.57276 6.38678 4.73977 8.34197C3.82783 9.3147 3.12709 10.2893 2.65439 11.0213C2.41836 11.3867 2.23999 11.6904 2.12133 11.9014C2.10133 11.937 2.08302 11.9699 2.06643 12ZM23.5 12C23.9472 11.7764 23.9471 11.7762 23.947 11.776L23.9459 11.7737L23.943 11.7681L23.933 11.7484C23.9243 11.7316 23.9118 11.7074 23.8953 11.6763C23.8624 11.6141 23.814 11.5244 23.7502 11.4111C23.6228 11.1846 23.434 10.8633 23.1856 10.4787C22.6896 9.71066 21.9528 8.6853 20.9898 7.65803C19.0728 5.61322 16.1998 3.5 12.5 3.5C8.80025 3.5 5.92724 5.61322 4.01023 7.65803C3.04717 8.6853 2.31041 9.71066 1.81436 10.4787C1.56602 10.8633 1.37719 11.1846 1.24976 11.4111C1.18602 11.5244 1.13759 11.6141 1.10469 11.6763C1.08824 11.7074 1.07567 11.7316 1.06701 11.7484L1.05696 11.7681L1.05414 11.7737L1.05328 11.7754C1.05317 11.7756 1.05279 11.7764 1.5 12L1.05279 11.7764C0.982405 11.9172 0.982405 12.0828 1.05279 12.2236L1.5 12C1.05279 12.2236 1.05268 12.2234 1.05279 12.2236L1.05328 12.2246L1.05414 12.2263L1.05696 12.2319L1.06701 12.2516C1.07567 12.2684 1.08824 12.2926 1.10469 12.3237C1.13759 12.3859 1.18602 12.4756 1.24976 12.5889C1.37719 12.8154 1.56602 13.1367 1.81436 13.5213C2.31041 14.2893 3.04717 15.3147 4.01023 16.342C5.92724 18.3868 8.80025 20.5 12.5 20.5C16.1998 20.5 19.0728 18.3868 20.9898 16.342C21.9528 15.3147 22.6896 14.2893 23.1856 13.5213C23.434 13.1367 23.6228 12.8154 23.7502 12.5889C23.814 12.4756 23.8624 12.3859 23.8953 12.3237C23.9118 12.2926 23.9243 12.2684 23.933 12.2516L23.943 12.2319L23.9459 12.2263L23.9467 12.2246C23.9468 12.2244 23.9472 12.2236 23.5 12ZM23.5 12L23.9472 12.2236C24.0176 12.0828 24.0174 11.9168 23.947 11.776L23.5 12Z" fill="#414141" />
                                    <path d="M9 12C9 10.067 10.567 8.5 12.5 8.5C14.433 8.5 16 10.067 16 12C16 13.933 14.433 15.5 12.5 15.5C10.567 15.5 9 13.933 9 12ZM12.5 9.5C11.1193 9.5 10 10.6193 10 12C10 13.3807 11.1193 14.5 12.5 14.5C13.8807 14.5 15 13.3807 15 12C15 10.6193 13.8807 9.5 12.5 9.5Z" fill="#414141" />
                                </svg>
                                Просмотреть заказ
                            </button>
                        </div>
                        : null
                }
            </div>
        </div>
    )
}

export default Order