import React, { useEffect, useMemo, useState } from 'react'
import cl from './WriteComment.module.css'
import { useIsAuth } from '../../store/isAuth'
import { useProducts } from '../../store/products'
import { useParams } from 'react-router-dom'
import Stars from '../stars/Stars'

const WriteComment = () => {
    const {id} = useParams()
    const products = useProducts(state => state.products)
    const product = useMemo(() => products.find(pr => pr.imgPath === id), [products, id])
    const isAuthUser = useIsAuth(state => state.isAuthUser)
    const isAuth = useIsAuth(state => state.isAuth)
    const addComment = useProducts(state => state.addComment)
    const [writenedComment, setWritenedComment] = useState(false)
    const [comment, setComment] = useState({
      id: null, date: '', text: '', rate: 1, userName: ''
    })

    const writeText = (text) => {
        setComment({...comment, text})
    }

    const voteRate = (args) => {
        setComment({...comment, rate: args.indexStar + 1 || 2})
        setUserRate(args.indexStar + 1)
    }

    const [userRate, setUserRate] = useState(0)
    

    const submitReview = () => {
        setComment({...comment, date: new Date().toLocaleDateString(), id: Date.now(), userName: isAuthUser.userName})
    }

    // add in basket

    const addInBasket = () => {
        
    }

    // - ---- ----- -

    useEffect(() => {
        if (comment.id && (comment.text || comment.rate)) {
            addComment(product.name, comment)
            setComment({id: null, date: '', text: '', rate: 0, userName: ''})
            setWritenedComment(true)
        }
    }, [comment.id])


  return (
    <>
        {isAuth ?
        <>
            {writenedComment ?
            <div>
                <div>Спасибо за ваш отзыв :)</div>
            </div>
            :
            <div className={cl.reviewsContainer}>
                <div className={cl.headerContainer}>
                    <div className={cl.title}>
                        Ваша оценка
                    </div>
                    <div className={cl.starsContainer}>
                        <Stars style={{cursor: 'pointer'}} rate={userRate} callback={voteRate}/>
                    </div>
                </div>
                
                <div className={cl.textContainer}>
                    <textarea value={comment.text} onChange={(e) => writeText(e.target.value)} placeholder='Отзыв' name="" id="" cols="30" rows="10"></textarea>
                </div>
                <div className={cl.buttonContainer}>
                    <button onClick={submitReview}>Отравить отзыв</button>
                </div>
            </div>}
        </>
        :
        <div>
            Авторизируйтесь для того чтобы оставить свой комментарий
            <img src="../../../../assets/logo/logo.svg" alt="logo" />
        </div>
        }
        
    </>
    
  )
}

export default WriteComment