import React from 'react'
import cl from './Slider.module.css'

const Slider = ({children}) => {
  return (
    <div className={cl.slider}>
        <div className={cl.content}>
            <div className={cl.image}/>
            <div className={cl.text}>
                {children}
            </div>
        </div>
    </div>
  )
}

export default Slider