import About from "../pages/About/About"
import Login from "../pages/Auth/Login"
import Basket from "../pages/Basket/Basket"
import Catalog from "../pages/Catalog/Catalog"
import CatalogProduct from "../pages/Catalog/CatalogProduct"
import Contacts from "../pages/Contacts/Contacts"
import MyFavourite from "../pages/Favorites/MyFavourite"
import MyMain from "../pages/Main/MyMain"
import Orders from "../pages/Orders/Orders"
import ProductPage from "../pages/Product/ProductPage"
import Vacations from "../pages/Vacancies/Vacations"


export const privateRoutes = [
    {path: '/basket', component: Basket, exact: true},
    {path: '/favourite', component: MyFavourite, exact: true},
    {path: '/orders', component: Orders, exact: true},
]

export const publicRoutes = [
    {path: '/', component: MyMain, exact: true},
    {path: '/login', component: Login, exact: true},
    {path: '/contacts', component: Contacts, exact: true},
    {path: '/about', component: About, exact: true},
    {path: '/vacations', component: Vacations, exact: true},
    {path: '/catalog', component: Catalog, exact: true},
    {path: '/catalog/:id', component: CatalogProduct, exact: true},
    {path: '/catalog/:id/productPage/:id', component: ProductPage, exact: true},
]


