import React from 'react'
import Container from '../UI/container/Container'
import cl from './Offers.module.css'

const Offers = () => {
    const banners = [
        {
            title: "Оформите карту «Северяночка»",
            subtitle: "И получайте бонусы при покупке в магазинах и на сайте",
            img: "banner1",
        },
        {
            title: "Покупайте акционные товары",
            subtitle: "И получайте вдвое больше бонусов",
            img: "banner2",
        },
    ];


    return (
        <Container>
            <title>Специальные предложения</title>
            <section>
                {banners.map(banner =>
                    <div key={banner.img} className={cl.banner}>
                        <div className={cl.text}>
                            <div className={cl.title}>{banner.title}</div>
                            <div className={cl.subtitle}>{banner.subtitle}</div>
                        </div>
                        <div className={cl.img}>
                            <img src={`./assets/offers/${banner.img}.svg`} alt="" />
                        </div>
                    </div>
                )}
            </section>
        </Container>
    )
}

export default Offers