import React from 'react'
import cl from '../Stars.module.css'

const Star = ({ rate, indexStar, callback, style }) => {
  const isCallback = () => typeof callback === 'function' ? callback({ rate, indexStar, callback, style }) : null

  return (
    <>
      {rate > indexStar ?
        <img style={style} src="./assets/icons/starHover.svg" onClick={isCallback} className={cl.starHover} alt="starHover" />
        :
        <img style={style} src="./assets/icons/star.svg" onClick={isCallback} className={cl.star} alt="star" />
      }
    </>
  )
}
export default Star