import React, { useMemo, useState } from 'react'
import Container from '../../components/UI/container/Container'
import Order from './Order'
import cl from './Orders.module.css'
import { useProducts } from '../../store/products'
import { useIsAuth } from '../../store/isAuth'
import { useUsers } from '../../store/users'

const Orders = () => {
  const {products} = useProducts()
  const {users} = useUsers()
  const {isAuthUser} = useIsAuth()

  const user = useMemo(() => users.find(user => user.userName === isAuthUser.userName), [isAuthUser])
  const userOrders = useMemo(() => user.orders, [user])
  const [ordersInPage, setOrdersInPage] = useState(1)
  return (
    <Container>
        <title>
            Заказы
        </title>
        <section>
            <div>
                {userOrders.map((order, index) => index < ordersInPage ?
                    <Order products={products} key={order.id} order={order}/>   
                    : null 
                )}
                {userOrders.length  > ordersInPage ?
                    <button className={cl.orderButton} onClick={() => setOrdersInPage(ordersInPage + 1)}>Посмотреть прошлый заказы</button>
                    : null
                }
            </div>
        </section>
    </Container>
  )
}

export default Orders