import React, { useMemo } from 'react'
import cl from './Comment.module.css'
import Stars from '../stars/Stars'
import { useUsers } from '../../store/users'

const Comment = ({ comment }) => {
    const users = useUsers(state => state.users)

    const user = useMemo(() => {
        return users.find(user => user.userName === comment.userName)
    }, [comment])

    return (
        <div key={comment.id} className={cl.comment}>
            <div className={cl.headerContainer}>
                <div className={cl.img}>
                    {user.userImg ?
                        <img width='40' height='40' src={`./assets/user/avatar/${user.userImg}.svg`} alt="" />
                        :
                        <img width='40' height='40' src={`./assets/user/avatar/default.svg`} alt="" />
                    }
                </div>
                <div className={cl.name}>
                    {comment.userName}
                </div>
            </div>
            <div className={cl.bodyContainer}>
                <div className={cl.rate}>
                    <Stars rate={comment.rate} />
                </div>
                <div className={cl.date}>
                    {comment.date}
                </div>
            </div>
            <div className={cl.textContainer}>
                <div className={cl.text}>
                    {comment.text}
                </div>
            </div>
        </div>
    )
}

export default Comment