import React, { useEffect, useMemo, useState } from 'react'
import cl from './ProductDetailed.module.css'
import Stars from '../stars/Stars'
import { getPromoPrice } from '../../utils/promo'
import { useIsAuth } from '../../store/isAuth'
import { useUsers } from '../../store/users'

const ProductDetailed = ({product, otherProductsInCatalog}) => {
    const isAuth = useIsAuth(state => state.isAuth)
    const isAuthUser = useIsAuth(state => state.isAuthUser)
    const users = useUsers(state => state.users)
    const toggleFavouriteProduct = useUsers(state => state.toggleFavouriteProduct)
    const addToBasket = useUsers(state => state.addToBasket)
    const [animation, setAnimation] = useState(false)

    const user = useMemo(() => {
        return users.find(user => user.userName === isAuthUser.userName)
    }, [users, isAuthUser])

    const favourite = () => {
        isAuth ? toggleFavouriteProduct(user.userName, product.name) : alert('Авторизируйтесь, чтобы добавить товар в избранное')
    }

    const basket = () => {
        if (isAuth) {
            setAnimation(true)
            addToBasket(product.name, user.userName)
            setTimeout(() => {setAnimation(false)}, 2000)
        }
        else alert('Авторизируйтесь, чтобы добавить товар в корзину')
    }

    const leftIMGS = [0,0,0,0,0]
    const rightIMGS = [0,0,0,0]

    const productions = [
        {title: 'Бренд', subtitle: 'ПРОСТОКВАШИНО'},
        {title: 'Страна производителя', subtitle: 'Россия'},
        {title: 'Упаковка', subtitle: '180 г'},
    ]

    const isFavourite = useMemo(() => {
        return user ? user.favourite.includes(product.name) : false 
    }, [users])

    return (
        <div className={cl.detailed}>
            <div className={cl.data}>
                <div className={cl.articul}>Арт. {Date.now()}</div>
                <div className={cl.rate}>
                    <Stars />
                </div>
                <div className={cl.reviews}>
                    {product.comments.length} Отзывов
                </div>
                <button className={cl.share} onClick={() => navigator.share({url: window.location.href})}>
                    <img src="../../../../assets/icons/share.svg" alt="share" />
                    Поделиться
                </button>
                <button className={cl.favourite} onClick={favourite}>
                    {isFavourite ? 
                    <img className={cl.icon} src="../../../../assets/icons/favouriteHover.svg" alt="favouriteHover" /> : 
                    <img className={cl.icon} src="../../../../assets/icons/favourite.svg" alt="favourite" />}
                    В избранное
                </button>
            </div>
            <div className={cl.container}>
                <div className={cl.leftIMGS}>
                    {leftIMGS.map((img, index) =>
                        <div key={index}>
                            <img  src={`../../../../assets/products/${product.imgPath}.png`} alt={product.imgPath} />    
                        </div>
                    )}
                </div>
                <div className={cl.middleIMG}>
                    <img src={`../../../../assets/products/${product.imgPath}.png`} alt={product.imgPath} />    
                </div>
                <div className={cl.descr}>
                    <div className={cl.priceProduct}>
                        <div className={cl.price}>
                            <div>
                                {product.price} BYN
                            </div>
                            <div>Обычная цена</div>
                        </div>
                        {!!product.promo && 
                        <div className={cl.promo}>
                            <div>
                                {getPromoPrice(product.price, product.promo)} BYN
                            </div>
                            <div>
                                С картой Северяночки
                                <img src="../../../../assets/icons/info.svg" alt="info" />
                            </div>
                        </div>}
                    </div>
                    {animation ? 
                        <button className={cl.addProduct}>Товар добален</button> :
                        <button onClick={basket}>
                            <img src="../../../../assets/icons/basket.svg" alt="basket" />
                            В корзину
                        </button>
                    }
                    <div className={cl.bonusInfo}>
                        <img src="../../../../assets/icons/bonus.svg" alt="bonus" />
                        Вы получаете {Math.round(product.price / 10)} бонусов
                    </div>
                    <div className={cl.notification}>
                        <img src="../../../../assets/icons/bell.svg" alt="bell" />
                        Уведомить о снижении цены
                    </div>
                    <div className={cl.productions}>
                        {productions.map(production =>
                            <div key={production.title} className={cl.production}>
                                <div className={cl.title}>{production.title}</div>
                                <div className={cl.subtitle}>{production.subtitle}</div>
                            </div>
                        )}
                    </div>
                </div>
                <div className={cl.rightIMGS}>
                    <div className={cl.title}>Похожие товары</div>
                    <div className={cl.containerRightIMGS}>
                        {rightIMGS.map((img, index) =>
                            <div key={index} className={cl.rightIMG}>
                                <img src={`../../../../assets/products/${product.imgPath}.png`} alt="" />
                                <div className={cl.price}>{product.price} BYN</div>
                            </div>    
                        )}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProductDetailed