import React from 'react'
import cl from './Filter.module.css'

const Filter = ({clearRange, minRangeValue, maxRangeValue, stock, updateApplyedFilter, setMaxRangeValue, setMinRangeValue, setStock}) => {
return ( 
    <div className={cl.filter}>
        <div className={cl.title}>Фильтр</div>
        <div className={cl.container}>
            <div className={cl.title}>Цена</div>
            <div className={cl.containerRange}>
                <div className={cl.inputsPrice}>
                    <input type="number" placeholder='1' min='1' value={minRangeValue} onChange={e => setMinRangeValue(e.target.value)}/>
                    <hr />
                    <input type="number" placeholder='100' min='1' value={maxRangeValue} onChange={e => setMaxRangeValue(e.target.value)}/>
                </div>
                <div className={cl.inputsRange}>
                    <input min='1' max='900' type="range" value={minRangeValue} onChange={(e) => setMinRangeValue(e.target.value)}/>
                    <input min='100' max='1000' type="range" value={maxRangeValue} onChange={(e) => setMaxRangeValue(e.target.value)}/>
                </div>
            </div>
            <div className={cl.containerStock}>
                <label className={cl.switch}>
                    <input type='checkbox' checked={stock} onChange={(e) => setStock(e.target.checked)}/>
                    <span className={[cl.slider, cl.round].join(' ')}></span>
                </label>
                <label>В наличии</label>
            </div>
            <button className={cl.apply} onClick={updateApplyedFilter}>
                Применить
            </button>
            <button className={cl.clear} onClick={clearRange}>
                Очистить
            </button>
        </div>
    </div>
)
}

export default Filter