import React, { useMemo } from 'react'
import WriteComment from '../comment/WriteComment'
import Comment from '../comment/Comment'
import cl from './Reviews.module.css'
import Stars from '../stars/Stars'
import Container from '../UI/container/Container'


const Reviews = ({product}) => {
  const reviewsExm = [0, 0, 0, 0, 0]
  const rate = useMemo(() => {
    return Math.round(product.comments.reduce((sum, comment) => sum + comment.rate, 0) / product.comments.length)
  }, [product])
  console.log(product)
  return (
    <Container>
        <title>Отзывы</title>
        <section>
            <div className={cl.mainContainer}>
                <div className={cl.left}>
                    <div className={cl.revTop}>
                        <Stars rate={rate}/>
                         <div>{rate} из 5</div>
                   </div>
                    <div className={cl.revMiddle}>
                        {reviewsExm.map((exm, index) =>
                        <div key={index} className={cl.point}>
                            <div className={cl.rate}>{5 - index}</div>
                            <div className={cl.exmStars}>
                                <Stars rate={5- index}/>
                            </div>
                            <div className={cl.count}>
                                {product.comments.filter(comment => comment.rate === 5 - index).length}
                            </div>
                        </div>    
                        )}
                    </div>
                </div>
                <div className={cl.comments}>
                    <div className={cl.commentsUsers}>
                        {product.comments.map(comment => 
                            <Comment key={comment.id} comment={comment}/>
                        )}
                    </div>
                    <WriteComment />
                </div>
            </div>
        </section>
    </Container>
  )
}

export default Reviews