import React, { useEffect, useState } from 'react'
import cl from './Container.module.css'

const Container = ({children, style}) => {
    const [title, setTitle] = useState(null)
    const [section, setSection] = useState(null)

    useEffect(() => 
        
        !children.length ? null : children.forEach(elem => elem.type === 'section' ? setSection(elem.props.children) : elem.type === 'title' ? setTitle(elem.props.children) : null)
    , [children])

  return (
    <div style={style} className={cl.container} >
        {title && <div className={cl.header}> {title} </div>}
        {section && <div className={cl.section}> {section} </div>}
    </div>
  )
}

export default Container