import React from 'react'
import Container from '../UI/container/Container'
import cl from './Articles.module.css'
const Articles = () => {
  const articles = [
    {
      imgPath: "articles-1",
      date: "05.03.2021",
      title: "Режим использования масок и перчаток на территории магазинов",
      subtitle:
        'Подробная информация о режимах использования масок и перчаток на территории магазинов "ЛЕНТА". Информация обновляется каждый будний день.',
      linkDetails: "#",
    },
    {
      imgPath: "articles-2",
      date: "05.03.2021",
      title: "Весеннее настроение для каждой",
      subtitle:
        "8 Марта – это не просто Международный женский день, это ещё день тюльпанов, приятных сюрпризов и праздничных тёплых пожеланий.",
      linkDetails: "#",
    },
    {
      imgPath: "articles-3",
      date: "22.02.2020",
      title: "ЗОЖ или ФАСТФУД. А вы на чьей стороне? Голосуем!",
      subtitle:
        "Голосуйте за любимые категории, выбирайте категорию-победителя в мобильном приложении и получайте кешбэк 10% баллами в апреле!",
      linkDetails: "#",
    },
  ]

  const styleForButton = {
    display: 'flex',
    gap: '5px',
    justifyContent: 'space-between',
    alignItems: 'center',
    alignSelf: 'stretch',
  }
  return (
    <Container>
      <title>
        <div>Статьи</div>
        <button style={styleForButton}>
          Все статьи
          <img src="./assets/icons/arrow.svg" alt="arrow" style={{ transform: 'rotate(-90deg)' }} />
        </button>
      </title>
      <section>
        {articles.map(article =>
          <article key={article.imgPath}>
            <div className={cl.img}>
              <img src={`./assets/articles/${article.imgPath}.png`} alt="" />
            </div>
            <div className={cl.descr}>
              <div className={cl.date}>{article.date}</div>
              <div className={cl.title}>{article.title}</div>
              <div className={cl.subtitle}>{article.subtitle}</div>
              <button>Подробнее</button>
            </div>
          </article>
        )}
      </section>
    </Container>
  )
}

export default Articles