import React, { useEffect, useMemo, useState } from 'react'
import ProductCard from '../../components/products/ProductCard'
import Filter from '../../components/filter/Filter'
import { useProducts } from '../../store/products'
import { useParams } from 'react-router-dom'
import { useCatalogs } from '../../store/catalogs'
import Container from '../../components/UI/container/Container'
import cl from './CatalogProducts.module.css'

const CatalogProduct = ({ childrenProducts }) => {
    const productsStore = useProducts(state => state.products)
    const catalogsStore = useCatalogs(state => state.catalogs)
    const { id } = useParams()

    const catalog = useMemo(() => {
        return catalogsStore.reduce((obj, item) => item.imgPath === id ? item : obj, {})
    }, [catalogsStore, id])

    const products = useMemo(() => {
        return productsStore.filter(product => product.catalog === catalog.name)
    }, [productsStore, catalog])

    const [minRangeValue, setMinRangeValue] = useState('')
    const [maxRangeValue, setMaxRangeValue] = useState('')
    const [stock, setStock] = useState(false)

    const [filterValue, setFilterValue] = useState({
        maxRangeValue: '',
        minRangeValue: '',
        stock: false
    })

    const [totalPages, setTotalPages] = useState(1)

    const [applyedFilter, setApplyedFilter] = useState([])

    const limitCardOnPage = 6

    const clearRange = () => {
        setMinRangeValue('')
        setMaxRangeValue('')
    }

    const clearAll = () => {
        setFilterValue({ ...filterValue, maxRangeValue: '', minRangeValue: '', stock: '' })
        setApplyedFilter([])
    }

    const clearNameFilter = (nameFilter) => {
        setFilterValue({ ...filterValue, [nameFilter]: '' })
        setApplyedFilter(applyedFilter.filter(filter => filter.nameFilter !== nameFilter))
    }

    const updateApplyedFilter = () => {
        setFilterValue({ maxRangeValue, minRangeValue, stock })
        const array = []
        if (minRangeValue !== '' && isFinite(+minRangeValue)) array.push({ nameFilter: ['minRangeValue'], text: `от ${minRangeValue} BYN` })
        if (maxRangeValue !== '' && isFinite(+maxRangeValue)) array.push({ nameFilter: ['maxRangeValue'], text: `до ${maxRangeValue} BYN` })
        if (stock) array.push({ nameFilter: ['stock'], text: `В наличии` })
        setApplyedFilter(array)
    }

    const filtredProducts = useMemo(() => {
        const use = childrenProducts || products
        return use.filter(p =>
            (p.price >= (filterValue.minRangeValue ? +filterValue.minRangeValue : p.price)) &&
            (p.price <= (filterValue.maxRangeValue ? +filterValue.maxRangeValue : p.price)) &&
            (p.stock ? filterValue.stock : true)  //default time
        )
    }, [applyedFilter, filterValue])

    const counterPages = useMemo(() => {
        const array = []
        const pages = Math.ceil(filtredProducts.length / limitCardOnPage)
        for (let i = 1; i <= pages; i++) {
            array.push(i)
        }
        return array
    }, [filtredProducts])

    useEffect(() => {
        if (+minRangeValue > +maxRangeValue) setMaxRangeValue(+minRangeValue + 5)
    }, [minRangeValue])

    useEffect(() => {
        if (+maxRangeValue < +minRangeValue) setMinRangeValue(+maxRangeValue - 5)
    }, [maxRangeValue])

    return (
        <Container>
            <title>
                {catalog ? catalog.name : null}
            </title>
            <section>
                <div className={cl.wrapper}>
                    <Filter
                        maxRangeValue={maxRangeValue}
                        minRangeValue={minRangeValue}
                        stock={stock}
                        setMaxRangeValue={setMaxRangeValue}
                        setMinRangeValue={setMinRangeValue}
                        setStock={setStock}
                        updateApplyedFilter={updateApplyedFilter}
                        clearRange={clearRange}
                    />
                    <div className={cl.productsSection}>
                        {!!applyedFilter.length &&
                            <div className={cl.haveFilters}>
                                {applyedFilter.map(filter =>
                                    <button className={cl.filter} onClick={() => clearNameFilter(filter.nameFilter)} key={filter.text} >
                                        {filter.text}
                                        <img src="./assets/icons/close.svg" alt="close" />
                                    </button>
                                )}
                                <button className={cl.clearFilters} onClick={clearAll}>
                                    Очистить Фильтры
                                    <img src="./assets/icons/close.svg" alt="close" />
                                </button>
                            </div>}
                        <div className={cl.products}>
                            {filtredProducts.map((product, index) => index >= limitCardOnPage * (totalPages - 1) && index < limitCardOnPage * totalPages ?
                                <ProductCard key={product.imgPath} product={product} /> : null
                            )}
                        </div>
                        <div className={cl.pagination}>
                            {totalPages > 2 &&
                                <div className={cl.superPrev} onClick={() => setTotalPages(1)}>
                                    <img src="./assets/icons/superPrev.svg" alt="superPrev" />
                                </div>}

                            {totalPages > 1 &&
                                <div className={cl.prev} onClick={() => setTotalPages(totalPages - 1)}>
                                    <img src="./assets/icons/prev.svg" alt="prev" />
                                </div>}

                            {counterPages.map(page =>
                                <div onClick={() => setTotalPages(page)} key={page} className={cl.countPage} style={{ cursor: 'pointer' }}>
                                    {page}
                                </div>
                            )}

                            {totalPages < counterPages[counterPages.length - 1] &&
                                <div className={cl.next} onClick={() => setTotalPages(totalPages + 1)}>
                                    <img src="./assets/icons/next.svg" alt="next" />
                                </div>}

                            {totalPages < counterPages[counterPages.length - 2] &&
                                <div className={cl.superNext} onClick={() => setTotalPages(counterPages.length)}>
                                    <img src="./assets/icons/superNext.svg" alt="superNext" />
                                </div>}
                        </div>
                    </div>
                </div>
            </section>
        </Container>
    )
}

export default CatalogProduct