import React from 'react'
import ProductCard from './ProductCard'
import Container from '../UI/container/Container'

const ProductsSection = ({ section, productsSection }) => {
    const styleForButton = {
        display: 'flex',
        gap: '5px',
        justifyContent: 'space-between',
        alignItems: 'center',
        alignSelf: 'stretch',
    }

    return (
        <Container>
            <title>
                <div>
                    {section.name}
                </div>
                <button style={styleForButton}>
                    {section.button}
                    <img src="./assets/icons/arrow.svg" alt="arrow" style={{ transform: 'rotate(-90deg)' }} />
                </button>
            </title>
            <section>
                {productsSection.map((product, index) =>
                    index < section.limitCard ? <ProductCard key={product.imgPath} product={product} /> : null
                )}
            </section>
        </Container>
    )
}

export default ProductsSection