import { create } from "zustand";
import { persist, devtools, createJSONStorage } from 'zustand/middleware'

export const useUsers = create(devtools(persist((set) => ({
  users: [
    {
      password: "321",
      userName: "Masloy",
      userImg: "avatar",
      card: { active: true, bonus: 200 },
      favourite: [],
      orders: [],
      basket: ["Нарезка", "Молоко", "Сосиски", "Йогурт"],
    },
    {
      password: "123",
      userName: "Kirill",
      userImg: "",
      favourite: ["Нарезка"],
      orders: [],
      basket: []
    },
    {
      userName: "Sabina",
      password: "sabina",
      userImg: "",
      favourite: [],
      basket: [],
      orders: [
        {
          id: 1,
          date: "19-01-2024",
          time: "14.00-18.00",
          result: "В процессе",
          orderProducts: [
            { name: "Нарезка", count: 5 },
            { name: "Молоко", count: 4 },
            { name: "Масло версия 1", count: 3 },
            { name: "Сосиски", count: 1 },
            { name: "Йогурт", count: 25 },
          ],
        },
        {
          id: 2,
          date: "18-01-2024",
          time: "14.00-18.00",
          result: "Возврат",
          orderProducts: [
            { name: "Масло версия 1", count: 3 },
            { name: "Сосиски", count: 1 },
          ],
        },
        {
          id: 3,
          date: "17-01-2024",
          time: "14.00-18.00",
          result: "Получен",
          orderProducts: [
            { name: "Нарезка", count: 5 },
            { name: "Сосиски", count: 1 },
          ],
        },
      ],
    },
  ],
  addUser: (user) =>
    set((state) => {
      const newUser = {
        userName: user.login,
        password: user.password,
        userImg: "",
        favourite: [],
        orders: [],
        basket: [],
        card: { active: false, bonus: 0 }
      }
      return { ...state, users: [...state.users, newUser] };
    }),
  toggleFavouriteProduct: (userName, productName) =>
    set((state) => {
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.userName === userName) {
            if (user.favourite.includes(productName))
              user.favourite.splice(user.favourite.indexOf(productName), 1);
            else user.favourite.push(productName);
          }
          return user;
        }),
      };
    }),
  addToBasket: (productName, userName) =>
    set((state) => {
      return { ...state, users: state.users.map((user) => {
        if (user.userName === userName) {
          user.basket.push(productName)
        }
        return user
      })};
    }),
  removeFromBasket: (productsUnSelected, userName) => {
    set((state) => {
      return  {...state, users: state.users.map((user) => {
        if (user.userName === userName) {
          user.basket = productsUnSelected.map(pr => pr.name)
        }
        return user
      })} 
    })
  },
  addBonusOnCards: (productName, userName, bonus) =>
    set((state) => {
      // functional
      return { ...state };
    }),
  addOrder: (userName, order) =>
    set((state) => {
      return { ...state, users: state.users.map((user) => {
        if (user.userName === userName) {
          user.orders = [order, ...user.orders]
          user.basket = []
        }
        return user
      }) };
    }),
}), {
  name: 'users',
  storage: createJSONStorage(() => sessionStorage)
})))
