import { createWithEqualityFn } from 'zustand/traditional'
import { persist, devtools, createJSONStorage } from 'zustand/middleware'
import { shallow } from 'zustand/shallow'

export const useIsAuth = createWithEqualityFn(devtools(persist((set, get) => ({
    isAuth: false,
    isAuthUser: {},
    login: (data) => set(state => ({isAuth: true, isAuthUser: data})),
    logout: () => set(state => ({isAuth: false, isAuthUser: {}}))
}), 
    {
        name: 'isAuth',
        storage: createJSONStorage(() => sessionStorage)
    }
)), shallow)
