import React from 'react'
import cl from './Contacts.module.css'
import Container from '../../components/UI/container/Container'
import Shops from '../../components/shops/Shops'

const Contacts = () => {
    const contacts = [
        { number: '+7 821 409 26 19', specialist: 'Бухгалтерия, склад', icon: 'home' },
        { number: '+7 908 716 33 97', specialist: 'Вопросы по системе лояльности', icon: 'percent' },
    ]

    return (
        <Container>
            <title>
                <div style={{ padding: '0px 10px' }}>Контакты</div>
            </title>
            <section>
                <div className={cl.container}>
                    <div className={cl.contacts}>
                        {contacts.map(contact =>
                            <div key={contact.number} className={cl.contact}>
                                <div className={cl.contactTitle}>
                                    <img src={`./assets/icons/${contact.icon}.svg`} alt={contact.icon} />
                                    {contact.specialist}
                                </div>
                                <div className={cl.tel}>
                                    <a href={`tel:${contact.number}`}>{contact.number}</a>
                                </div>
                            </div>
                        )}
                    </div>
                    <Shops />
                </div>
            </section>
        </Container>
    )
}

export default Contacts