import React, { useEffect, useMemo, useState } from 'react'
import Container from '../../components/UI/container/Container'
import cl from './Basket.module.css'
import BasketProduct from './BasketProduct'
import { getPromoPrice } from '../../utils/promo'
import { useProducts } from '../../store/products'
import { useIsAuth } from '../../store/isAuth'
import { useUsers } from '../../store/users'

const Basket = () => {

    const { products } = useProducts()
    const { users, removeFromBasket, addOrder } = useUsers()
    const { isAuthUser, isAuth } = useIsAuth()
    const user = useMemo(() => users.find(user => user.userName === isAuthUser.userName))
    const userBasket = useMemo(() => user.basket)


    const [productsInBasket, setProductsInBasket] = useState([])

    const [bonusActivated, setBonusActivated] = useState(false)
    const [delivery, setDelivery] = useState(false)
    const [deliveryTime, setDeliveryTime] = useState(
        [{ time: '8:00-14:00', available: true, checked: true },
        { time: '14:00-18:00', available: true, checked: false },
        { time: '18:00-20:00', available: true, checked: false },
        { time: '20:00-22:00', available: false, checked: false },]
    )
    const [deliveryDate, setDeliveryDate] = useState('')
    const [deliveryAddress, setDeliveryAddress] = useState({
        locality: '',
        street: '',
        house: '',
        apartment: '',
        additionally: ''
    })

    useEffect(() => {
        setProductsInBasket(userBasket.reduce((arr, pr) => [...arr, { ...products.find(product => product.name === pr), count: 1, select: false }], []))
    }, [userBasket, user, users])


    const resultSum = useMemo(() => {
        return productsInBasket.reduce((sum, cur) => getPromoPrice(cur.price, cur.promo) * cur.count + sum, 0)
    }, [productsInBasket])

    const addCount = (name, count, index) => {
        setProductsInBasket(productsInBasket.map((pr, ind) => {
            if (pr.name === name && count >= 1 && index === ind) pr.count = count
            return pr
        }))
    }

    const addSelect = (name, select, index) => {
        setProductsInBasket(productsInBasket.map((pr, prIndex) => {
            if (pr.name === name && index === prIndex) pr.select = select
            return pr
        }))
    }

    const allSelected = (event) => {
        setProductsInBasket(productsInBasket.map(pr => {
            pr.select = !pr.select
            return pr
        }))
    }

    const dates = useMemo(() => {
        const date = []
        for (let i = 0; i < 14; i++) {
            const d = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate() + i)
            if (d.getDay() === 6 || d.getDay() === 0) continue
            date.push(d)
        }
        return date
    }, [])

    useEffect(() => {
        setDeliveryDate(dates[0].toLocaleDateString())
    }, [dates])

    const deleteProductFromBasket = (event) => {
        removeFromBasket(productsInBasket.filter(product => !product.select), isAuthUser.userName)
    }

    const resultProductsInBasket = useMemo(() => {
        return productsInBasket.reduce((sum, cur) => sum + cur.count, 0)
    }, [productsInBasket])

    const createOrder = (event) => {
        const { locality, street, house, apartment } = deliveryAddress
        if (locality === '' || street === '' || house === '' || apartment === '') return

        const order = {
            id: Date.now(),
            date: deliveryDate,
            time: deliveryTime.find(deliv => deliv.checked).time,
            result: "В процессе",
            orderProducts: productsInBasket,
        }

        addOrder(user.userName, order)

        setIsAddOrder(true)
    }

    const [isAddOrder, setIsAddOrder] = useState(false)

    const checkout = (event) => {
        return resultSum > 1000 ? setDelivery(true) : null
    }


    const checkDate = (event) => {
        setDeliveryTime(deliveryTime.map(func => {
            if (func.time === event.target.value) func.checked = true
            else func.checked = false
            return func
        }))
    }


    return (
        <Container>
            <title>
                {!productsInBasket.length ?
                    isAddOrder ? `Спасибо за сделанный заказ :D` : `На данный момент корзина пуста.
            Добавляйте товар в коризну чтобы увидеть их здесь!`
                    :
                    delivery ? "Доставка" : 'Корзина'}
            </title>
            <section>
                {!productsInBasket.length ? null : <form className={cl.basketContainer} onSubmit={(event) => event.preventDefault()}>
                    {delivery ?
                        <div className={cl.deliverySection}>
                            <Container>
                                <title>
                                    Куда
                                </title>
                                <section>
                                    <div className={cl.where}>
                                        <div className={cl.point}>
                                            <div>Населенный пункт</div>
                                            <select defaultValue={deliveryAddress.locality} onChange={e => setDeliveryAddress({ ...deliveryAddress, locality: e.target.value })} required>
                                                <option value disabled selected>Выберите населенный пункт</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                                <option value="Усть-Ижма">Усть-Ижма</option>
                                            </select>
                                        </div>
                                        <div className={cl.street}>
                                            <div>Улица</div>
                                            <input value={deliveryAddress.street} onChange={e => setDeliveryAddress({ ...deliveryAddress, street: e.target.value })} type="text" placeholder='Пушкина' required />
                                        </div>
                                        <div className={cl.home}>
                                            <div>Дом</div>
                                            <input value={deliveryAddress.house} onChange={e => setDeliveryAddress({ ...deliveryAddress, house: e.target.value })} type="text" placeholder='11' required />
                                        </div>
                                        <div className={cl.kvartira}>
                                            <div>Квартира</div>
                                            <input value={deliveryAddress.apartment} onChange={e => setDeliveryAddress({ ...deliveryAddress, apartment: e.target.value })} type="text" placeholder='12' required />
                                        </div>
                                        <div className={cl.dop}>
                                            <div>Дополнительно</div>
                                            <input value={deliveryAddress.additionally} onChange={e => setDeliveryAddress({ ...deliveryAddress, additionally: e.target.value })} type="text" placeholder='A' />
                                        </div>
                                    </div>
                                </section>
                            </Container>
                            <Container>
                                <title>
                                    Когда
                                </title>
                                <section>
                                    <div className={cl.when}>
                                        <div className={cl.date}>
                                            <div>Дата</div>
                                            <select defaultValue={deliveryDate} onChange={e => setDeliveryDate(e.target.value)} required>
                                                {dates.map(date =>
                                                    <option key={date} defaultValue={date.toLocaleDateString()}>{date.toLocaleDateString()}</option>
                                                )}
                                            </select>
                                        </div>
                                        <div className={cl.time}>
                                            <div>Время</div>
                                            <div>
                                                {deliveryTime.map((t, index) =>
                                                    <div key={t.time} className="form_radio_btn">
                                                        <input disabled={!t.available} id={`radio-${index + 1}`} checked={t.checked} type="radio" name="radio" defaultValue={t.time} onChange={checkDate} required />
                                                        <label htmlFor={`radio-${index + 1}`}>{t.time}</label>
                                                        <span>
                                                            <img src="./assets/icons/clock.svg" alt="clock" />
                                                            На это время доставить не можем
                                                        </span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </section>
                            </Container>
                        </div>
                        :
                        <div className={cl.leftSide}>
                            <div className={cl.leftHeader}>
                                <div className={cl.allDelete}>
                                    <div className={cl.checkbox}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                                            <path d="M3 8.00001C3 7.81591 3.14924 7.66667 3.33333 7.66667H12.6667C12.8508 7.66667 13 7.81591 13 8.00001C13 8.1841 12.8508 8.33334 12.6667 8.33334H3.33333C3.14924 8.33334 3 8.1841 3 8.00001Z" fill="white" />
                                        </svg>
                                    </div>
                                    <button onClick={allSelected}>Выделить всё</button>
                                </div>
                                <div className={cl.deleteChecked}>
                                    <button onClick={deleteProductFromBasket}>Удалить выбранные</button>
                                </div>
                            </div>
                            <div className={cl.products}>
                                {productsInBasket.map((prod, index) =>
                                    <BasketProduct addSelect={addSelect} index={index} addCount={addCount} key={index} product={prod} />
                                )}
                            </div>
                        </div>
                    }
                    <div className={cl.rightSide}>
                        {(isAuthUser.card ? isAuthUser.card.active ? true : false : false) ?
                            <div className={cl.bonus}>
                                <div>
                                    <label className='switch'>
                                        <input type='checkbox' checked={bonusActivated} onChange={e => setBonusActivated(e.target.checked)} />
                                        <span className={[cl.slider, 'round', 'slider'].join(' ')}></span>
                                    </label>
                                    <label>Списать {isAuthUser.card.bonus} BYN</label>
                                </div>
                                <div className={cl.bonusCard}>На карте накоплено {isAuthUser.card.bonus} BYN</div>
                            </div>
                            : null}
                        <div className={cl.description}>
                            <div className={cl.descriptionProduct}>
                                <div>{resultProductsInBasket} товара</div>
                                <div>{resultSum}</div>
                            </div>
                            {bonusActivated && isAuthUser.card.active ?
                                <div className={cl.descriptionPromo}>
                                    <div>Скидка</div>
                                    <div>-{isAuthUser.card.bonus} BYN</div>
                                </div>
                                : null}
                        </div>
                        <div className={cl.result}>
                            <div className={cl.resutlSum}>
                                <div>Итог</div>
                                <div>{resultSum - (isAuthUser.card ? isAuthUser.card.active ? isAuthUser.card.bonus : 0 : 0)} BYN</div>
                            </div>
                            <div className={cl.resultBonus}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="12" viewBox="0 0 25 12" fill="none">
                                    <path d="M23.6883 0.666611H1.31196C0.843143 0.666611 0.460308 1.05823 0.503301 1.52051C0.994639 7.0113 6.17826 11.3333 12.5001 11.3333C18.822 11.3333 24.0056 7.01332 24.497 1.52051C24.5379 1.05823 24.1571 0.666611 23.6883 0.666611Z" fill="#70C05B" />
                                </svg>
                                {`Вы получаете ${(resultSum - (isAuthUser.card ? isAuthUser.card.active ? isAuthUser.card.bonus : 0 : 0)) / 10} бонусов`}
                            </div>
                        </div>
                        {delivery ?
                            <div className={cl.btns}>
                                <button type='submit' onClick={createOrder} className={cl.checkOnSite}>Оплатить на сайте</button>
                                <button type='submit' onClick={createOrder} className={cl.checkOnGet}>Оплатить при получении</button>
                            </div>
                            :
                            <div className={cl.btns}>
                                {resultSum < 1000 &&
                                    <div className={cl.min}>
                                        Минимальная сумма заказа 1000 BYN
                                    </div>}
                                <button className={cl.order} onClick={checkout}>Оформить заказ</button>
                            </div>
                        }


                    </div>
                </form>}
            </section>
        </Container>
    )
}

export default Basket