import React, { useEffect, useMemo, useState } from 'react'
import ProductsSection from '../../components/products/ProductsSection'
import Reviews from '../../components/reviews/Reviews'
import Container from '../../components/UI/container/Container'
import ProductDetailed from '../../components/products/ProductDetailed'
import { useUsers } from '../../store/users'
import { useProducts } from '../../store/products'
import { useCatalogs } from '../../store/catalogs'
import { useParams } from 'react-router-dom'

const ProductPage = () => {
    const [sections, setSections] = useState([
        {name: 'Акции', buttonName: 'Все акции', limitCard: 3},
        {name: 'Новинки', buttonName: 'Все новинки', limitCard: 4},
        {name: 'Покупали раньше', buttonName: 'Все покупки', limitCard: 4},
        {name: 'С этим товаром также покупают', buttonName: '', limitCard: 4}
    ])
    const {users} = useUsers()
    const {products} = useProducts()
    const catalogs = useCatalogs(state => state.catalogs)
    const {id} = useParams()

    
    const product = useMemo(() => {
        return products.reduce((obj, product) => product.imgPath === id ? product : obj,{})
    }, [id, products])

    const catalogProducts = useMemo(() => {
        return products.filter(pr => pr.catalog === product.catalog)
    }, [products, product])

    const [rates, setRates] = useState([0, 0, 0, 0, 0])


    const addRate = (i) => {
        setRates(rates.map((star, index) => index <= i ? 1 : 0))
    }

    useEffect(() => {
        addRate(product.rate - 1)
    }, [])

    return (
    <div className='productPage'>
        <Container>
            <title>{product.name}</title>
            <section>
                <ProductDetailed otherProductsInCatalog={catalogProducts} product={product}/>
            </section>
        </Container>
        
        <div className="productPage-alsoBye">
            <ProductsSection section={sections[3]} productsSection={products}/>
        </div>
        <Reviews users={users} product={product}/>
        <div className="productPage-additionally">
            <ProductsSection section={sections[0]} productsSection={products} />
        </div>
    </div>
  )
}

export default ProductPage