import React from 'react'
import cl from './BasketProduct.module.css'
import { getPromoPrice } from '../../utils/promo'

const BasketProduct = ({ product, addCount, addSelect, index }) => {

    const incrementCount = (event) => {
        addCount(product.name, product.count + 1, index)
    }

    const decrementCount = (event) => {
        addCount(product.name, product.count - 1, index)
    }

    return (
        <div className={cl.section}>
            <div className={cl.img}>
                <img src={`./assets/products/${product.imgPath}.png`} alt={product.imgPath} />
            </div>
            <div className={cl.descr}>
                <div className={cl.title}>{product.name}</div>
                <div className={cl.container}>
                    {!!product.promo &&
                        <div className={cl.promo}>
                            <div className={cl.price}>{getPromoPrice(product.price, product.promo)} BYN</div>
                            <div className={cl.text}>С картой</div>
                        </div>
                    }
                    <div className={cl.normal}>
                        <div className={cl.price}>{product.price} BYN</div>
                        <div className={cl.text}>Обычная</div>
                    </div>
                    за шт.
                </div>
            </div>
            <div className={cl.btns}>
                <button onClick={decrementCount}>
                    <img src="./assets/icons/minus.svg" alt="minus" />
                </button>
                <div>{product.count}</div>
                <button onClick={incrementCount}>
                    <img src="./assets/icons/plus.svg" alt="plus" />
                </button>
            </div>
            <div className={cl.containerPrice}>

                {!!product.promo && <div className={cl.promo}>{getPromoPrice(product.price, product.promo) * product.count} BYN{getPromoPrice}</div>}
                <div className={cl.normal}>{product.price * product.count} BYN</div>
            </div>
            <div className={cl.block}>
                <input type="checkbox" checked={product.select} onChange={(e) => addSelect(product.name, e.target.checked, index)} />
                {product.select &&
                    <div style={{ backgroundColor: '#70C05B' }}>
                        <img src="./assets/icons/checkBasket.svg" alt="checkBasket" />
                    </div>
                }
            </div>
        </div>
    )
}

export default BasketProduct